<template>
  <div class="container">
    <div class="row">
      <div class="col-12 pt-1">
        <h1>Создать счет</h1>
      </div>
      <div class="col-12 pt-1">
        <!--Город и категория-->
        <div class="row pt-1">
          <div class="col-6">
            <div class="form-group">
              <label for="city">Город<span class="text-danger">*</span></label>
              <select class="form-control" id="city" required v-model="city_selected">
                <option
                    v-for="city in cities"
                    :key="city.ID"
                    :value="city.ID"
                >{{city.VALUE}}</option>
              </select>
            </div>
          </div>
          <div class="col-6 pt-1">
            <div class="form-group">
              <label for="category">Категория<span class="text-danger">*</span></label>
              <select class="form-control" id="category" :disabled="!city_selected"  v-model="category_selected" required>
                <option
                    v-for="category in categoriesByCity"
                    :key="category.id"
                    :value="category.id"
                >{{category.name}}</option>
              </select>
            </div>
          </div>
        </div>
        <!--От какого юр.лица платим-->
        <div class="row pt-1">
          <div class="col-12">
            <label for="company">От какого юр.лица платим<span class="text-danger">*</span></label>
            <VueMultiselect
                v-model="companySelected"
                :close-on-select="true"
                :options="companyByCity"
                placeholder="Компания"
                label="name"
                :taggable="true"
            >
            </VueMultiselect>
          </div>
        </div>
        <!--Получать платежный документ-->
        <div class="row pt-1">
          <div class="col-12 pt-2 pb-2">
            <div class="form-check form-check-inline">
              <input class="form-check-input" type="checkbox" id="document_pay" v-model="document_pay">
              <label class="form-check-label" for="document_pay">Получать платежный документ</label>
            </div>
          </div>
        </div>
        <!--Желаемая дата оплаты-->
        <div class="row">
          <div class="col-12">
            <label for="exampleFormControlSelect1">Желаемая дата оплаты</label>
            <input class="form-control" type="date" v-model="date_pay_selected">
          </div>
        </div>
        <!--ИНН,КПП,Название,ОГРН-->
        <div class="row pt-2">
          <div class="col-6 position-relative">
            <label for="inn">ИНН<span class="text-danger">*</span></label>
            <input class="form-control" id="inn" type="text" required style="padding-right: 25px;" v-model="inn_selected">
            <svg @click="searchCompany"
                 xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-search position-absolute" viewBox="0 0 16 16">
              <path d="M11.742 10.344a6.5 6.5 0 1 0-1.397 1.398h-.001c.03.04.062.078.098.115l3.85 3.85a1 1 0 0 0 1.415-1.414l-3.85-3.85a1.007 1.007 0 0 0-.115-.1zM12 6.5a5.5 5.5 0 1 1-11 0 5.5 5.5 0 0 1 11 0z"/>
            </svg>
          </div>
          <div class="col-6">
            <label for="exampleFormControlSelect1">Название<span class="text-danger">*</span></label>
            <input class="form-control" type="text" required v-model="name_selected">
          </div>
          <div class="col-6 pt-1">
            <label for="exampleFormControlSelect1">КПП<span class="text-danger">*</span></label>
            <input class="form-control" v-model="kpp_selected" type="text">
          </div>
          <div class="col-6 pt-1">
            <label for="exampleFormControlSelect1">ОГРН</label>
            <input class="form-control" v-model="ogrn_selected" type="text">
          </div>
        </div>
        <!--За что платим-->
        <div class="row pt-1">
          <div class="col-12">
            <label for="description">За что платим<span class="text-danger">*</span></label>
            <textarea class="form-control" id="description" v-model="description_selected" rows="3" required></textarea>
          </div>
        </div>
        <!--Сумма-->
        <div class="row pt-1">
          <div class="col-12">
            <label for="description">Сумма<span class="text-danger">*</span></label>
            <input class="form-control" type="number" min="1" v-model="sum_selected" required>
          </div>
        </div>
        <!--Вложения-->
        <div class="row pt-1">
          <div class="col-12">
            <span>Вложения<span class="text-danger">*</span></span>
          </div>
          <div
              v-for="inputFile in fileInputCount"
              :key="'file'+inputFile"
              class="col-12 pb-1">
            <input type="file" @change="uploadFile($event)" class="form-control-file" name="file">
          </div>
        </div>
      </div>
    </div>
    <div class="row pb-2">
      <div class="col-12 mt-2">
        <button type="button" class="add-btn btn btn-success col-12" @click="createBill">
          Создать заявку
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import VueMultiselect from 'vue-multiselect'
import BillService from "@/services/BillService";
import axios from "axios";
import telegramClient from "@/services/TelegramClient";
export default {
  name: 'BillAdd',
  components: { VueMultiselect },
  data: function (){
    return {
      chatId: this.$router.id,
      document_pay: null,
      date_pay_selected: null,
      city_selected: null,
      cities: [],
      category_option_selected:null,
      category_selected:null,
      categories: [],
      companySelected:null,
      companyByCity:[],
      categoriesByCity: [],
      inn_selected: null,
      name_selected: null,
      ogrn_selected: null,
      kpp_selected: null,
      description_selected:null,
      fileInputCount: 1,
      file_selected: [],
      sum_selected:null
    };
  },
  methods:{
    uploadFile: function (event){
      if (event.target.files.length) {
        this.fileInputCount++;
      }
    },
    searchCompany: function (){
      let remote_url = 'https://suggestions.dadata.ru/suggestions/api/4_1/rs/findById/party';
      let data = {
        'query' : this.inn_selected,
        'count' : 10
      };

      let config = {
        headers: {'Authorization': 'Token ' + process.env.VUE_APP_DADATA_API_KEY}
      };
      axios.post(remote_url, data, config).then(result => {
        if (result.data) {
          if (result.data.suggestions.length) {
            const company = result.data.suggestions.pop();
            if (company) {
              this.name_selected = company.value;
              this.kpp_selected = company.data.kpp;
              this.ogrn_selected = company.data.ogrn;
            }
          }
        }
      }).catch(error => {
        // тут обработка ошибки или вывод в консоль
        console.log(error);
      });
    },
    validation: function (){
      let isSuccess = true;

      if (!this.city_selected) isSuccess = false;
      if (!this.category_selected) isSuccess = false;
      if (!this.inn_selected) isSuccess = false;
      if (!this.name_selected) isSuccess = false;
      if (!this.description_selected) isSuccess = false;
      if (!this.companySelected) isSuccess = false;
      if (!this.sum_selected) isSuccess = false;
      this.file_selected = this.getFiles();
      if (this.file_selected.length === 0) isSuccess = false;

      return isSuccess;
    },
    getFiles: function (){
      const arFiles = [];
      document.querySelectorAll('[name="file"]')
          .forEach(item => {
            if (item.files.length) {
              arFiles.push(item.files[0])
            }
          })
      return arFiles;
    },
    createBill: function (){
      if (this.validation())
      {
        BillService.createBill({
          chat_id:this.chatId,
          title: this.description_selected,
          city: this.city_selected,
          category: this.category_selected,
          company:this.companySelected,
          data_pay: this.date_pay_selected,
          document_pay: this.document_pay,
          sum: this.sum_selected,
          counterparty:{
            inn: this.inn_selected,
            name: this.name_selected,
            kpp: this.kpp_selected,
            ogrn: this.ogrn_selected,
          },
          files:this.getFiles(),
        })
        .then(result => {
          if (result.data.result) {
            let idBill = result.data.result;
            telegramClient.getTg().sendData(`Счет №${idBill} успешно создан.`);
          }
        }).catch(e => {
          alert(e.message)
        });
      } else {
        telegramClient.getTg().showAlert("Необходимо заполнить все обязательные поля");
      }
    },
  },
  watch:{
    city_selected: function (city){
      this.companySelected = null;
      this.category_option_selected = null;

      const categoriesByCity = [...this.categories]
          .filter(categoryItem => {
            return categoryItem.city === city;
          });
      this.categoriesByCity = categoriesByCity;

      BillService.getCompany(city)
          .then(result => {
            if (result.data) {
              this.companyByCity = result.data.result;
            }
          })
    },
  },
  created() {
    this.chatId = this.$route.params.id;
    // расширяем на все окно
    telegramClient.getTg().expand();

    console.log(telegramClient.getUserData())

    BillService.getLib()
        .then(result => {
          if (result.data) {
            this.cities = result.data.result.CITIES;
            this.categories = result.data.result.CATEGORIES;
          }
    });
  }
}
</script>
<style src="vue-multiselect/dist/vue-multiselect.css"></style>
<style scoped>
.add-btn{
  background: #4ECB4A;
  border-color: #4ECB4A;
}
.bi-search{
  bottom: 12px;
  right: 18px;
  cursor: pointer;
}
</style>
