import BitrixService from "@/services/BitrixService";

export default {
    /**
     * Получить справочники
     * Города
     * Категории
     * @returns {Promise<AxiosResponse<any>>}
     */
    getLib(){
        return BitrixService().get('bill.get.lib')
    },
    /**
     * Получить спиоск компаний далимо по городу
     * @param cityId
     * @returns {Promise<AxiosResponse<any>>}
     */
    getCompany(cityId)
    {
        return BitrixService().get('bill.get.company',{
            params: {
                CITY:cityId
            }
        })
    },
    /**
     * Поиск контрагента по ИНН
     * @param inn
     * @returns {Promise<AxiosResponse<any>>}
     */
    getCounterpartyByInn(inn)
    {
        return BitrixService().get('bill.get.сounterparty',{
            params: {
                INN:inn
            }
        })
    },
    createBill(fields)
    {
        return BitrixService().post('bill.telegram.add', fields,{
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }
        )
    }
}