import axios from 'axios'

export default() => {
    return axios.create({
        baseURL: process.env.VUE_APP_REST_URL + process.env.VUE_APP_REST_KEY + '/',
        withCredentials: false,
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        }
    })
}